*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body{
  background-color: rgba(253, 4, 87, 0.918);
  width: 75%;
  padding: 10vh 0;
  margin: 0 auto;
  line-height: 1.5;
  /* letter-spacing: 1px; */
}
.header-cont{
  text-align: center;
}
.main-h{
text-transform: uppercase;
font-size: 4rem;
color: #150050;
}
.description{
  font-weight: 200;
  font-size: 2rem;
  color: #fff;
}
.description span{
  display: block;
}
.form-flex{
  display: flex;
  width: 70%;
  margin: 0 auto;
}
.form-col{
  width: 100%;
  margin: .5rem 0;
}
input, select{
  padding: 10px;
  width: 100%;
  border-radius: 5px;
  border: 0;
}
.selectLabel{
  color: #EFEFEF;
  padding-left: 4%;
}
button{
  padding: .7rem;
  width: 25%;
  margin: 0 auto;
  border-radius: 10px;
  color: #fff;
  border: 0;
  background-color: #150050;
  transition: all .3s ease-in-out;
}
button:hover{
  background-color: black;
  padding: .8rem;
  width: 26%;
}
.submit{
  margin: 2rem 0;
  text-align: center;
}

@media (max-width:425px) {
  
body{
  width: 95%;
}
.header-cont{
  margin: 3rem 0;
}
.main-h{
font-size: 2rem;
}
.description{
  font-size: 1rem;
}
.form-flex{
  display: flex;
  width: 80%;
}
}